@import "../../../../assets/styles/app";

.customize {

  .header {
    height: 48px;
    padding-left: 12px;
    padding-right: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $color_lines;

    p {
      font-weight: 700;
      font-size: 12px;
      line-height: 140%;
      text-transform: uppercase;
      color: $color_main_100;
    }

    .openBtn {
      border: 1px solid $color_lines;
      width: 32px;
      height: 32px;

      svg {
        transform: rotate(90deg);
        transition: 0.3s;
      }

      &_open {
        svg {
          transform: rotate(-90deg);
        }
      }
    }


  }

  .description {
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: $color_body_50;
    padding: 12px 12px 24px;
    border-bottom: 1px solid $color_lines;
  }

}